"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs" // 탭 기능 추가
import { 
  Plus, Play, Square, Trash2, Edit, Send, 
  Slack, LogOut, Activity, BarChart3, Users, 
  Loader2, Sparkles, MessageSquare, Settings,
  CheckCircle2, XCircle, FileText, UserCheck
} from "lucide-react"

// 전문가 신청 데이터 인터페이스
interface AdvisorRequest {
  id: string;
  userName: string;
  name: string;
  appliedAt: string;
  status: "PENDING" | "APPROVED" | "REJECTED";
}

interface Promotion {
  id: number;
  title: string;
  points: number;
  limit: number;
  status: "READY" | "ACTIVE" | "FINISHED";
}

export default function AdminDashboard() {
  const router = useRouter()
  const [isAuthorized, setIsAuthorized] = useState(false)
  
  // 1. 프로모션 상태
  const [promotions, setPromotions] = useState<Promotion[]>([
    { id: 1, title: "선착순 1000명 포인트 증정", points: 5000, limit: 1000, status: "READY" },
    { id: 2, title: "오픈 기념 웰컴 포인트", points: 3000, limit: 500, status: "ACTIVE" },
  ])

  // 2. 전문가 신청 내역 상태 (임시 데이터)
  const [advisorRequests, setAdvisorRequests] = useState<AdvisorRequest[]>([
    { id: "1", userName: "kakao_123", name: "홍길동", appliedAt: "2025-12-15", status: "PENDING" },
    { id: "2", userName: "google_456", name: "김철수", appliedAt: "2025-12-16", status: "PENDING" },
  ])

//   useEffect(() => {
//     const role = localStorage.getItem("role")
//     // MANAGER 권한 체크 (이전 로그인 로직과 통일)
//     if (role !== "MANAGER") {
//       router.replace("/login")
//     } else {
//       setIsAuthorized(true)
//     }
//   }, [router])

  // 전문가 승인/거절 처리 함수
  const handleAdvisorAction = (id: string, action: "APPROVED" | "REJECTED") => {
    const message = action === "APPROVED" ? "승인하시겠습니까?" : "거절하시겠습니까?";
    if (confirm(message)) {
      setAdvisorRequests(prev => 
        prev.map(req => req.id === id ? { ...req, status: action } : req)
      )
      alert(`${action === "APPROVED" ? "승인" : "거절"} 처리가 완료되었습니다.`);
    }
  }

  const toggleStatus = (id: number, status: "ACTIVE" | "FINISHED") => {
    setPromotions(prev => prev.map(p => p.id === id ? { ...p, status } : p))
  }

  const handleDelete = (id: number) => {
    if(confirm("정말 삭제하시겠습니까?")) setPromotions(prev => prev.filter(p => p.id !== id))
  }

  const handleLogout = () => {
    if (confirm("로그아웃 하시겠습니까?")) {
      localStorage.clear()
      router.push("/login")
    }
  }

//   if (!isAuthorized) {
//     return (
//       <div className="min-h-screen flex flex-col items-center justify-center bg-background">
//         <Loader2 className="h-10 w-10 animate-spin text-primary mb-4" />
//         <p className="text-sm text-muted-foreground animate-pulse">관리자 권한 확인 중...</p>
//       </div>
//     )
//   }

  return (
    <div className="min-h-screen bg-background">
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/admin" className="flex items-center gap-2">
            <Image src="/logo.png" alt="Next Me" width={120} height={32} className="h-8 w-auto" priority />
            <Badge variant="outline" className="ml-2 border-primary text-primary font-bold">ADMIN</Badge>
          </Link>
          <div className="flex items-center gap-4">
            <Button variant="ghost" size="icon"><MessageSquare className="h-5 w-5" /></Button>
            <Button variant="ghost" size="icon"><Settings className="h-5 w-5" /></Button>
            <Button variant="ghost" size="icon" onClick={handleLogout} title="로그아웃">
              <LogOut className="h-5 w-5 text-muted-foreground hover:text-destructive transition-colors" />
            </Button>
            <Avatar className="border"><AvatarFallback className="bg-primary/5 italic">AD</AvatarFallback></Avatar>
          </div>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8">
        <div className="flex justify-between items-end mb-8">
          <div>
            <h1 className="text-3xl font-bold mb-2 text-foreground">시스템 관리</h1>
            <p className="text-muted-foreground font-medium">프로모션 및 전문가 신청 현황을 관리합니다</p>
          </div>
        </div>

        {/* 상단 현황판 */}
        <div className="grid md:grid-cols-3 gap-6 mb-8">
          <Card className="p-6 border-l-4 border-l-green-500">
            <p className="text-sm text-muted-foreground mb-1 font-bold">서버 상태</p>
            <p className="text-2xl font-black text-green-600">Operational</p>
          </Card>
          <Card className="p-6 border-l-4 border-l-primary">
            <p className="text-sm text-muted-foreground mb-1 font-bold">진행 프로모션</p>
            <p className="text-2xl font-black text-primary">{promotions.filter(p=>p.status==="ACTIVE").length} 건</p>
          </Card>
          <Card className="p-6 border-l-4 border-l-orange-500">
            <p className="text-sm text-muted-foreground mb-1 font-bold">전문가 신청 대기</p>
            <p className="text-2xl font-black text-orange-500">{advisorRequests.filter(r=>r.status==="PENDING").length} 건</p>
          </Card>
        </div>

        <Tabs defaultValue="promotions" className="space-y-6">
          <TabsList className="bg-muted/50 p-1">
            <TabsTrigger value="promotions" className="font-bold">프로모션 관리</TabsTrigger>
            <TabsTrigger value="advisors" className="font-bold">전문가 신청 내역</TabsTrigger>
          </TabsList>

          {/* 탭 1: 프로모션 관리 */}
          <TabsContent value="promotions">
            <Card className="p-6">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-xl font-bold flex items-center gap-2"><Sparkles className="h-5 w-5 text-primary" /> 프로모션 목록</h2>
                <Button size="sm"><Plus className="h-4 w-4 mr-1" /> 생성</Button>
              </div>
              <div className="overflow-x-auto">
                <table className="w-full text-left">
                  <thead className="text-xs font-bold uppercase border-b">
                    <tr>
                      <th className="px-2 py-3">이벤트명</th>
                      <th className="px-2 py-3">포인트</th>
                      <th className="px-2 py-3">상태</th>
                      <th className="px-2 py-3 text-right">제어</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y text-sm">
                    {promotions.map((p) => (
                      <tr key={p.id} className="hover:bg-secondary/10 transition-colors">
                        <td className="px-2 py-4 font-bold">{p.title}</td>
                        <td className="px-2 py-4 text-blue-600 font-bold">{p.points.toLocaleString()}P</td>
                        <td className="px-2 py-4">
                          <Badge variant={p.status === "ACTIVE" ? "default" : "secondary"}>{p.status}</Badge>
                        </td>
                        <td className="px-2 py-4 text-right flex justify-end gap-1">
                          {p.status === "READY" && <Button size="icon" variant="ghost" className="text-green-600" onClick={() => toggleStatus(p.id, "ACTIVE")}><Play className="h-4 w-4 fill-current"/></Button>}
                          <Button size="icon" variant="ghost" onClick={() => handleDelete(p.id)}><Trash2 className="h-4 w-4"/></Button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </Card>
          </TabsContent>

          {/* 탭 2: 전문가 신청 내역 (신규 추가된 섹션) */}
          <TabsContent value="advisors">
            <Card className="p-6 border-orange-200">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-xl font-bold flex items-center gap-2"><UserCheck className="h-5 w-5 text-orange-500" /> 전문가 승인 대기열</h2>
              </div>
              <div className="overflow-x-auto">
                <table className="w-full text-left">
                  <thead className="text-xs font-bold uppercase border-b">
                    <tr>
                      <th className="px-2 py-3">신청자</th>
                      <th className="px-2 py-3">ID</th>
                      <th className="px-2 py-3">신청일</th>
                      <th className="px-2 py-3">상태</th>
                      <th className="px-2 py-3 text-right">결정</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y text-sm">
                    {advisorRequests.length === 0 ? (
                      <tr><td colSpan={5} className="py-10 text-center text-muted-foreground">신청 내역이 없습니다.</td></tr>
                    ) : (
                      advisorRequests.map((req) => (
                        <tr key={req.id} className="hover:bg-orange-50/30 transition-colors">
                          <td className="px-2 py-4 font-bold">{req.name}</td>
                          <td className="px-2 py-4 text-muted-foreground">{req.userName}</td>
                          <td className="px-2 py-4">{req.appliedAt}</td>
                          <td className="px-2 py-4">
                            <Badge variant={req.status === "PENDING" ? "outline" : req.status === "APPROVED" ? "default" : "destructive"}>
                              {req.status}
                            </Badge>
                          </td>
                          <td className="px-2 py-4 text-right">
                            {req.status === "PENDING" ? (
                              <div className="flex justify-end gap-2">
                                <Button size="sm" variant="outline" className="text-green-600 border-green-200 hover:bg-green-50" onClick={() => handleAdvisorAction(req.id, "APPROVED")}>
                                  <CheckCircle2 className="h-4 w-4 mr-1" /> 승인
                                </Button>
                                <Button size="sm" variant="outline" className="text-red-600 border-red-200 hover:bg-red-50" onClick={() => handleAdvisorAction(req.id, "REJECTED")}>
                                  <XCircle className="h-4 w-4 mr-1" /> 거절
                                </Button>
                              </div>
                            ) : (
                              <span className="text-xs text-muted-foreground">처리완료</span>
                            )}
                          </td>
                        </tr>
                      ))
                    )}
                  </tbody>
                </table>
              </div>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  )
}